.contact-us{
    background-color: #f1f1f1;
    color: #fff;
    min-width: 100%

  }
  .right_conatct_social_icon{
       background: linear-gradient(to top right, #6662e2 20%, #00b7ff 100%);
  }
  .contact_us{
      padding: 120px 0px;
      font-family: 'Poppins', sans-serif;
  }
  
  .contact_inner{
      background-color: #fff;
      position: relative;
      box-shadow: 20px 22px 44px #cccc;
      border-radius: 25px;
  }
  .contact_field{
      padding: 60px 340px 120px 100px;
  }
  .right_conatct_social_icon{
      height: 100%;
  }
  
  .contact_field h3{
     color: #000;
      font-size: 40px;
      letter-spacing: 1px;
      font-weight: 600;
      margin-bottom: 10px
  }
  .contact_field p{
      color: #000;
      font-size: 13px;
      font-weight: 400;
      letter-spacing: 1px;
      margin-bottom: 35px;
  }
  .contact_field .form-control{
      border-radius: 0px;
      border: none;
      border-bottom: 1px solid #ccc;
  }
  .contact_field .form-control:focus{
      box-shadow: none;
      outline: none;
      border-bottom: 2px solid #1325e8;
  }
  .contact_field .form-control::placeholder{
      font-size: 13px;
      letter-spacing: 1px;
  }
  
  .contact_info_sec {
      position: absolute;
      background-color: #2d2d2d;
      right: 1px;
      top: 12%;
      height: 29rem;
      width: 26rem;
      padding: 40px;
      border-radius: 25px 0 0 25px;
  }
  .contact_info_sec h4{
      letter-spacing: 1px;
      padding-bottom: 15px;
  }
  
  .info_single{
      margin: 22px 0px;
  }
  .info_single i{
      margin-right: 15px;
  }
  .info_single span{
      font-size: 14px;
      letter-spacing: 1px;
  }
  
  button.contact_form_submit {
      background: linear-gradient(to top right, #6662e2 -5%, #00b7ff 100%);
      border: none;
      color: #fff;
      padding: 10px 15px;
      width: 100%;
      margin-top: 25px;
      border-radius: 35px;
      cursor: pointer;
      font-size: 14px;
      letter-spacing: 2px;
  }
  .socil_item_inner li{
      list-style: none;
  }
  .socil_item_inner li a{
      color: #fff;
      margin: 0px 15px;
      font-size: 14px;
  }
  .socil_item_inner{
      padding-bottom: 10px;
  }
  .form-control{
    padding:1rem 0.75rem;
  }
  @media screen and (max-width:1000px){
    .contact_info_sec{
        position: unset;
        width: 100%;
        border-radius: 0 0 0 25px;
    } 
    .contact_info_sec{
        padding: 60px;
        height: auto;
    }
    .contact_field{
        padding: 44px 10px 44px 10px;
    }
  }
  @media screen and (max-width:767px){
    .contact_info_sec{
        position: unset;
        width: 100%;
        border-radius: 0 0 25px 25px;
    } 
    .contact_info_sec{
        padding: 40px 30px ;
    }
  }